<template>
  <div class="modal">
    <div class="mainer">
      <div class="modal-body">
        <h2 class="name">{{product.name}}</h2>
        <div class="parameters">
          <b>Размер:</b>
          {{product.size}}
        </div>
        <div class="parameters">
          <b>Материал:</b>
          {{product.material}}
        </div>

        <carousel
          class="carousel"
          :perPage="1"
          :loop="true"
          :adjustableHeight="true"
          :paginationEnabled="false"
          :navigationEnabled="true"
          :autoplay="autoplay"
          :navigationPrevLabel="`<span style='font-size: 100px; color: #fff; line-height: 20px;'>&#8249;</span>`"
          :navigationNextLabel="`<span style='font-size: 100px; color: #fff; line-height: 20px;'>&#8250;</span>`"
        >
          <slide v-for="(img, index) in product.urlGallery" :key="index">
            <div class="slide-block">
              <div class="btn-conteiner">
                <svg
                  class="svg-btn svg-btn--share"
                  title="копировать в буфер"
                  @click="sharePhoto(`${product.id}`-`${index+1}`)"
                >
                  <use xlink:href="../images/svg/sprite.svg#share" />
                </svg>
                <svg class="svg-btn" title="включить автопрокрутку" @click="autoplay = !autoplay">
                  <use v-if="!autoplay" xlink:href="../images/svg/sprite.svg#play" />
                  <use v-else xlink:href="../images/svg/sprite.svg#stop" />
                </svg>
              </div>
              <div class="btn-conteiner btn-conteiner--remove" title="закрыть" @click="closeModal">
                <svg class="svg-btn">
                  <use xlink:href="../images/svg/sprite.svg#remove" />
                </svg>
              </div>
              <div class="img-block">
                <img
                  class="img-block__photo"
                  :ref="`${product.id}`-`${index+1}`"
                  :src="require('../images/products/' + product.id + img)"
                />
              </div>
            </div>
          </slide>
        </carousel>
      </div>
    </div>
  </div>
</template>

<script>
import { Carousel, Slide } from "vue-carousel";

export default {
  name: "modal",
  components: { Carousel, Slide },
  props: ["product"],
  data() {
    return {
      autoplay: false,
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal");
    },
    sharePhoto(ref) {
      const link = this.$refs[ref][0].currentSrc;
      navigator.clipboard
        .writeText(link)
        .then(() => {
          console.log(link);
        })
        .catch((err) => {
          console.log("Something went wrong", err);
        });
    },
  },
};
</script>

